.inputText {
  @include outlineStyling($formElement_borderColor);
  @include textInputSizing($formElement_height, $global-medium_fontSize, $utility-baseUnit);
  @include standardBorderStyles;
  @include placeholderStyling {
    color: $formElement-placeholderColor;
  }
  display: block;
  position: relative;
  width: 100%;
  vertical-align: middle;
  color: $formElement_fg;
  background-color: $formElement_bg;
  padding: $formElement_paddingVertical $formElement_horizontalPadding;
  outline: 0;
}

.inputText-inlineLabel {
  font-size: $global-medium_fontSize;
  height: $formElement_height;
  padding: 0 #{$utility-baseUnit * 2};
  line-height: $formElement_height;
}
